.header_container {
    width: 100%;
    height: 20vh;
    background-color: #21222A;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}

.header_container div {
    margin: 0 30px;
}

.header_container h1 {
    color: #61DAFB;
}

.logo {
    width: 70px;
}

@media (max-width: 720px) {
    .header_container {
        justify-content: center;
    }
}