.content {
    background-color: #33373E;
    min-height: 100vh;
    width: 100%;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    flex-direction: row;
}

.content ul {
    padding: 35px;
}

.content ul li {
    font-size: 18px;
}

.content ul li::marker {
    font-size: 1.5em;
    color: #61DAFB;
}

.facts h2 {
    font-size: 4em;
}

.image {
    filter: invert(1);
    width: 350px;
}

@media (max-width: 800px) {
    .content {
        min-height: 80vh;
        justify-content: center;
        flex-direction:column-reverse;
        font-size: 16px;
    }

    .image {
        width: 200px;
    }

    .content ul li {
        font-size: 16px;
    }

    .facts h2 {
        font-size: 3em;
        text-align: center;
        padding: 10px;
    }

}